@mixin headerFont {
  font-size: 12px;
  text-transform: uppercase;
  font-weight: 500;
}

@mixin starSize($size) {
  width: $size;
  height: $size;
}

@mixin linkIndents {
  margin: 0 10px;
  padding: 23px 0;
}

@mixin contentSize {
  width: 100%;
  height: 4px;
}

@mixin imageSize {
  width: 160px;
  height: 134px;
}
